body {
  background-color: #f1edee;
  color: #686963;
}

a.nav-link {
  color: #db5461;
  font-weight: 700;
}

p {
  font-weight: 500;
}

p.top {
  text-indent: 50px;
  line-height: 25px;
  padding: 10px;
}

#bio {
  line-height: 40px;
  font-size: 25px;
  padding: 20px;
  text-indent: 0px;
}

a.link {
  display: table;
}

h1.title {
  margin-top: 20px;
  display: flex;
  justify-content: center;
}

h1.contact {
  margin-bottom: 20px;
  padding-top: 10px;
}

h3 {
  color: #db5461;
}

ul {
  margin-bottom: 30px;
  font-size: 20px;
}

img.cat {
  max-height: 700px;
  padding-top: 20px;
}

div.resumeContainer {
  display: flex;
  justify-content: center;
}

img.resume {
  max-height: 1100px;
}
